import './目标画面.scss';
import 应许之地 from './资源/视频/rm.mp4';
import 静默独奏 from './资源/视频/sqs.mp4';
import 监禁之城 from './资源/视频/tlj.mp4';

export default function 目标画面({ 目标名 }) {
  const 视频表 = {
    应许之地,
    监禁之城,
    静默独奏,
  };
  return (
    <div className="目标画面">
      <span className="提示">暂无目标画面</span>
      <video autoPlay loop src={视频表[目标名]} />
    </div>
  );
}
